<template>
  <div class="board panels on">
    <ul class="list">
        <router-link 
            :to="{name:'MusicPlay',
            params:{
                songid:currentData.id,
                songname:currentData.name,
                songauther:currentData.auther,
                songpicurl:currentData.picUrl,
                songmp3url:currentData.mp3url
            }}"
            tag="li" 
            v-for="(item, index) in currentData" :key="index"
            class="song url"
        >
            <div class="poster">
                <img :src="currentData.picUrl" alt="">
            </div>
            <div class="info">
                <div class="name">
                    {{ currentData.name }}
                </div>
                <div class="author">{{ currentData.auther }}</div>
            </div>
        </router-link>
    </ul>
    <div class="more-songs url">
        查看榜单&gt;
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            currentData:[]
        }
    },
    props:{
        url:{
            type:String,
            default:""
        }
    },
    mounted() {
        this.$axios.get(this.url)
        .then(res => {
            console.log(res.data.info);
            this.currentData = res.data.info
        })
        .catch(error => {
            console.log("抱歉！江酱music没拿到数据！",error);
        })
    },
};
</script>

<style scoped>
    .panel {
        border-top: 1px solid #eee;
        position: relative;
        top: -1px;
        display: block;
        background: #fff;
    }
    ul {
        background-color: white;
    }

    .list {
        padding: 20px;
        padding-top: 0;
    }
    li {
        margin-top: 1px;
        margin-bottom: 10px;
        display: flex;
    }
    .panel .list li {
        height: 50px;
        border-bottom: 1px solid #eee;
        padding: 0;
        padding-top: 10px;
        background-color: white;
    }
    .panel .list li .poster {
        position: relative;
        width: 45px;
        margin-right: 10px;
    }
    img {
        width: 60px;
        border: 1px solid #eee;
    }
    .info {
        margin-left: 10px;
        margin-top: 8px;
    }
    .info .author {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 12px;
        color: #999;
        margin-top: 2px;
    }

    .more-songs {
        color: #999;
        margin-top: -5px;
        font-size: 12px;
        text-align: center;
        height: 32px;
        line-height: 32px;
    }
</style>


