<template>
    <div class="search">
        <div class="search-title">
            <input type="text" placeholder="请输入内容" v-model="searchContent">
            <button type="button" @click="searchHandler" name="button">搜索</button>
        </div>
        <ul class="list searchlist">
            <router-link 
                tag="li" 
                :to="{
                    name:'MusicPlay',
                        params:{
                        songid:songlist.id,
                        songname:songlist.name,
                        songauther:songlist.auther,
                        songpicurl:songlist.picUrl,
                        songmp3url:songlist.mp3url
                    }}" 
                class="song"
            >
                <div class="left">
                    <div class="info single-line">
                        <div>
                            <span>{{ songlist.name }}</span>
                        </div>
                        <span class="txt">{{ songlist.auther }}</span>
                    </div>
                </div>
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
    name:"Search",
    data() {
        return {
            searchContent:'',
            songlist:[]
        }
    },
    methods:{
        searchHandler(){
            let searchURL = "https://api.vvhan.com/api/rand.music?type=json&sort=热歌榜" + this.searchContent
            this.$axios.get(searchURL)
            .then(res => {
                if (res.data.info == 22001) {
                    this.$message("江酱music没有此条info")
                    return
                }
                this.songlist = res.data.info
                console.log(res.data.info);
            })
            .catch(error => {
                console.log(error);
            })
        }
    }

}
</script>

<style scoped>
    .search-title {
        padding: 20px;
        overflow: hidden;
        clear: both;
    }
    input {
        width: 80%;
        height: 30px;
        line-height: 30px;
        background: #fff;
        border: 1px solid #f1f2f3;
        padding-left: 10px;
        float: left;
        display: inline-block;
    }
    button {
        float: left;
        width: 15%;
        height: 30px;
    }
    .list {
        word-wrap: break-word;
        hyphens: auto;
        word-break: break-all;
        border-bottom: 1px solid #e5e5e5;
        border-top: 1px solid #e5e5e5;
    }
    .list li.song {
        justify-content: space-between;
        min-height: 55px;
        text-align: left;
    }
    li {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        box-sizing: border-box;
        min-height: 50px;
        border: 1px solid #f2f2f2;
        padding-left: 10px;
    }
    .list .item.song .left,
    .list li.song .left {
        display:flex;
        flex-direction:row;
        justify-content:flex-start;
        align-items:center;
        flex:1;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .list .item .info,
    .list li .info {
        flex:1;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .list li .info>span {
        font-weight:400;
        display:block;
        font-size:12px;
        color:#999;
    }
</style>


