<template>
    <div class="play">
        <div class="header">
            <div class="title">
                <router-link to="/">
                    <i class="iconfont icon-shouye left"></i>
                </router-link>
                <div class="music-info">
                    <p class="nowmusic">{{ this.$route.params.songname }}</p>
                    <p class="author">{{ this.$route.params.songauther }}</p>
                </div>
                <router-link to="/">
                    <i class="iconfont icon-sousuo right"></i>
                </router-link>
            </div>
        </div>

        <div class="song-info">
            <div class="song-info-img">
                <img :src="this.$route.params.songpicurl" alt="">
            </div>
            <div class="iconbox">
                <i class="iconfont icon-shoucang left"></i>
                <i class="box"></i>
                <i class="iconfont icon-xiazai right"></i>
            </div>

        </div>
        <div class="song">
            <audio 
                ref="player" 
                controls
                autoplay
                :src="this.$route.params.songmp3url"
            >
            </audio>
        </div>
    </div>
</template>

<script>
export default {
    name:"musicplay",
    data() {
        return {
            
        }
    },
    
}
</script>

<style scoped>
    .header {
        padding: 15px;
    }
    .music-icon {
        flex: 1;
        font-size: 20px;
    }
    .music-info .nowmusic {
        font-size: 18px;
    }
    .music-info .author {
        font-size: 16px;
        color: gray;
    }
    .title {
        display: flex;
        text-align: center;
        justify-content: space-between;
    }
    .ca {
        color: red;
    }
    .song-info {
        padding: 15px;
    }
    .song-info-img {
        display: flex;
        text-align: center;
        justify-content: center;
    }
    .song-info-img img {
        width: 50%;
        border-radius: 5px;
        box-shadow: 0 0 10px rgba(50, 50, 50, .31);
    }
    .song-lrc {
        margin-top: 10px;
        min-height: 50px;
    }

    .iconbox {
        display: flex;
        margin-top: 30px;
    }
    .iconbox .box {
        flex: 1;
    }
    .song {
        width: 100%;
        text-align: center;
    }
    .song audio {
        width: 80%;
    }
    .active {
        color: #222;
    }
    i {
        font-size: 28px;
    }
</style>


