<template>
    <div class="home">
        <!-- 今日推荐模块 -->
        <Today_Recommend 
            title="今日推荐"
            url="https://api.vvhan.com/api/rand.music?type=json&sort=飙升榜"
        /> 
        <!-- 新歌推荐模块 -->
        <News_Music />
        <!-- 轮播图模块 -->
        <Swiper_Banner />
        <!-- 音乐列表模块 -->
        <MusicListNav />
        <!-- 页面不用重复获取数据 -->
        <keep-alive>    
            <!-- 各种音乐榜单 -->
            <router-view />
        </keep-alive>
        <!-- 热门歌单 -->
        <Today_Recommend 
            title="热门歌单"
            url="https://api.vvhan.com/api/rand.music?type=json&sort=新歌榜"
        /> 
    </div>
</template>

<script>
import News_Music from "../components/News_Music";
import Swiper_Banner from '../components/Swiper_Banner';
import Today_Recommend from "../components/Today_Recommend"
import MusicListNav from "./musiclist/Music_Listnav"    //音乐列表

export default {
    name:"Home",
    components:{ Today_Recommend, News_Music, Swiper_Banner,MusicListNav }

}
</script>

<style scoped>

</style>


