<template>
    <div class="mod-albums">
        <div class="hd log url">
            <h2>新歌推荐</h2>
            <!-- <div style="font-size:14px;">
                更多
            </div> -->
        </div>
        <div class="container">
            <div class="gallery">
                <div class="scroller">
                    <router-link 
                        tag="div" 
                        :to="{name:'MusicPlay',
                        params:{
                            songid:newsMusic.id,
                            songname:newsMusic.name,
                            songauther:newsMusic.auther,
                            songpicurl:newsMusic.picUrl,
                            songmp3url:newsMusic.mp3url
                        }}"
                        class="card url" 
                        v-for="(item,index) in newsMusic" :key="index"
                    >
                        <div class="album">
                            <img :src="newsMusic.picUrl" :alt="newsMusic.name">
                            <div class="name">{{ newsMusic.name }}</div>
                            <div class="author">{{ newsMusic.auther }}</div>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name:'newsMusic',
    data() {
        return {
            newsMusic:[]
        }
    },
    mounted(){
        let url = 'https://api.vvhan.com/api/rand.music?type=json&sort=新歌榜'
        this.$axios.get(url)
        .then(res => {
            this.newsMusic = res.data.info
            console.log(res.data.info);
        })
        .catch(error => {
            console.log("抱歉！江酱music没拿到数据！",error);
        })
    }
}
</script>

<style scoped>
    .mod-albums {
        background-color: #fff;
        padding: 10px 17px;
        margin-top: 10px;
    }

    .hd {
        display: flex;
        margin: 14px 0 18px 0;
    }
    .hd h2 {
        flex: 1;
        margin: 0;
        padding: 0;
        font-size: 24px;
    }
    .hd div {
        width: 64px;
        font-size: 12px;
        text-align: right;
    }
    .mod-albums .gallery {
        overflow: hidden;
        margin: 0 -5px;
    }
    .mod-albums .gallery .scroller {
        height: 145px;
        overflow: hidden;
    }
    .mod-albums .gallery .card {
        width: 33.3%;
        float: left;
        box-sizing: border-box;
        padding: 0 5px 10px;
    }
    .mod-albums .gallery .card .album {
        position: relative;
    }
    .mod-albums .gallery .card img {
        width: 100%;
        height: auto;
        border: 1px solid #eee;
    }
    .mod-albums .gallery .card .name {
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-top: 4xp;
        line-height: 14px;
        max-height: 28px;
        margin-bottom: 2px;
    }
    .mod-albums .gallery .card .author {
        font-size: 10px;
    }
</style>


