<template>
    <div class="mod-albums">
        <div class="hd log url">
            <h2>{{ title }}</h2>
            <!-- <div style="font-size:14px;">
                更多
            </div> -->
        </div>
        <div class="container">
            <div class="gallery">
                <div class="scroller">
                    <router-link 
                        tag="div" 
                        :to="{name:'MusicPlay',
                        params:{
                            songid:todayRecommend.id,
                            songname:todayRecommend.name,
                            songauther:todayRecommend.auther,
                            songpicurl:todayRecommend.picUrl,
                            songmp3url:todayRecommend.mp3url
                        }}"
                        class="card url" 
                        v-for="(item,index) in todayRecommend" :key="index"
                    >
                        <div class="album">
                            <img :src="todayRecommend.picUrl" :alt="todayRecommend.name">
                            <div class="name">{{ todayRecommend.name }}</div>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name:'todayRecommend',
    data() {
        return {
            todayRecommend:[

            ]
        }
    },
    props:{
        title:{
            type:String,
            default:'江酱音乐'
        },
        url:{
            type:String,
            default:'https://api.vvhan.com/api/rand.music?type=json&sort=新歌榜'
        }
    },
    mounted(){
        let url = this.url
        this.$axios.get(url)
        .then(res => {
            this.todayRecommend = res.data.info
            console.log(res.data.info);
        })
        .catch(error => {
            console.log("抱歉！江酱music没拿到数据！",error);
        })
    }
}
</script>

<style scoped>
    .mod-albums {
        background-color: #fff;
        padding: 10px 17px;
    }
    .hd {
        display: flex;
        margin: 14px 0 18px 0;
    }
    .hd h2 {
        flex: 1;
        margin: 0;
        padding: 0;
        font-size: 24px;
    }
    .hd div {
        width: 64px;
        font-size: 12px;
        text-align: right;
    }
    .mod-albums .gallery {
        height: 130px;
        overflow: hidden;
        margin: 0 -5px;
    }
    .mod-albums .gallery .card {
        width: 33.3%;
        float: left;
        box-sizing: border-box;
        padding: 0 5px 10px;
    }
    .mod-albums .gallery .card .album {
        position: relative;
    }
    .mod-albums .gallery .card img {
        width: 100%;
        height: auto;
        border: 1px solid #eee;
    }
    .mod-albums .gallery .card .name {
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-top: 4xp;
        line-height: 14px;
        max-height: 28px;
        margin-bottom: 2px;
    }
</style>


