import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from "axios"
// 引入icon图标
import './assets/fonts/iconfont.css'


Vue.prototype.$axios = Axios
Vue.prototype.HOST = "/baidu_music_api"

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



