<template>
    <div class="banner">
        <swiper :options="swiperOptions">
            <swiper-slide>
                <img src="../assets/img/bannner1-Jay.jpg" alt="" />
            </swiper-slide>
            <swiper-slide>
                <img src="../assets/img/banner3-Eason.jpg" alt="" />
            </swiper-slide>
            <swiper-slide>
                <img src="../assets/img/banner1-WuYueTian.jpg" alt="" />
            </swiper-slide>

            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  data() {
    return {
      swiperOptions: {
        pagination : '.swiper-pagination',
        autoplay:1500,
      },
    };
  },
  components: {
    swiper,
    swiperSlide,
  }
};
</script>

<style scoped>
.banner {
  padding: 10px;
}
.banner img {
    width: 100%;
    height: 170px;
    overflow: hidden;
}
</style>


