import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Home from '@/pages/Home'                 //主界面
import ListCard from '@/pages/ListCard'         //榜单界面
import SingerList from '@/pages/SingerList'     //歌手列表界面
import MineView from '@/pages/MineView'         //我的界面
import Search from '@/pages/Search'             //搜索界面
import HostList from '@/pages/musiclist/Host_List'        //热歌榜界面
import OriginalList from '@/pages/musiclist/Original_List'        //原创榜界面
import NewsList from '@/pages/musiclist/News_List'        //新歌榜界面
import MusicPlay from '@/pages/MusicPlay'        //播放页界面




Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/musicplay',
      name:"MusicPlay",
      component:MusicPlay
    },
    {
      path: '/',
      name: 'Index',
      redirect:'/Home/hot', //进来就渲染Home组件
      component: Index,
      children:[
        {
          path:'home',
          component:Home,
          children:[
            {
              path:'hot',
              component:HostList,
            },
            {
              path:'original',
              component:OriginalList
            },
            {
              path:'news',
              component:NewsList
            }

          ]
        },
        {
          path:'listcard',
          component:ListCard
        },
        {
          path:'singerlist',
          component:SingerList
        },
        {
          path:'mineview',
          component:MineView
        },
        {
          path:'search',
          component:Search
        },
        
      ]
    },
    
  ]
})



